*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

body,
html{
  overflow-x: hidden;
  font-family: "poppins", sans-serif;
}

.wgl{
  position: absolute;
  top:0;
  left:0;
  /* width: 100%;
  height: 100vh; */
  z-index: 1;
}

nav{
  color :white;
  z-index: 2;
  position: relative;
  padding: 4rem 8rem;
  display: flex;
  justify-content: space-between;
}
nav a{
  text-decoration: none;
  color: white;
  font-weight: bold;
}

nav ul{
  display:flex;
  list-style: none;
  gap: 2rem;
}

.title{
  /* position: absolute; */
  color: white;
  z-index: 2;
  position: absolute;
  font-size: 2rem;
  left: 50%;
  top: 75%;
  transform: translate(-50%, -25%);

}